import { Component, ViewChild } from '@angular/core';
import { NavController, NavParams,Content } from 'ionic-angular';
import { AngularFirestore, AngularFirestoreModule  } from 'angularfire2/firestore';
// import { AngularFirestoreModule } from 'angularfire2/firestore';
// import { AngularFirestoreModule } from 'angularfire2/firestore';
import { AngularFireDatabase } from 'angularfire2/database';
import { Observable } from 'rxjs/Observable';
import  'rxjs/add/operator/switchMap';

// export interface Item { username: string, message: string }

/**
 * Generated class for the ChatPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

// @IonicPage()
@Component({
	selector: 'page-chat',
	templateUrl: 'chat.html',
})

export class ChatPage {
	@ViewChild(Content) content: Content;

	username:string ="";
	message:string ="";
	items: Observable<any[]>;
	setMyClasses(user) {
		let classes = {
		  floatLeft:!this.username==user,
		  floatRight:this.username==user,
		};
		return classes;
	}

	constructor(public navCtrl: NavController, public navParams: NavParams, public db: AngularFirestore, public afm : AngularFirestoreModule, public afDB: AngularFireDatabase) {
		this.username=this.navParams.data.username;
		this.items=afDB.list('chats').valueChanges();
		console.log('constructor loaded');
		// this.content.scrollToBottom();
		// this.scrollto();
	}

	sendMessage(){
		console.log('Sending msg');
		if(this.message!=""){
			this.afDB.list('chats').push({
				username:this.username,
				message:this.message,
				joined:false,
				left:false		
			});
		}

		this.message="";

		this.content.scrollToBottom();
		this.content.scrollToBottom();
	}
	eventHandler(keyCode){
		console.log('eventHandler Sending msg');
		
		if(keyCode==13){
			this.afDB.list('chats').push({
				username:this.username,
				message:this.message,
				joined:false,
				left:false		
			});
	
			this.message="";
	
			this.content.scrollToBottom();
			this.content.scrollToBottom();
		}
	   
	}

	ionViewDidEnter() {
		console.log('ionViewDidEnter ChatPage');
		// // this.content.scrollToBottom();

		// var element = document.getElementById("myLabel");
		// // I can't remember why I added a short timeout, 
		// // but you might be able to use ngzone instead.
		// // the below works great though. 
		// console.log(element);
		// element.scrollIntoView(true);
		// setTimeout(()=>{element.scrollIntoView(true)},200);

		this.afDB.list('chats').push({
			username:this.username,
			message:"",
			joined:true,
			left:false		
		});
		this.content.scrollToBottom();
		// setTimeout(() => {
		// 	this.content.scrollToBottom();
		// }, 300);
	}

	ionViewDidLoad() {
		// console.log('ionViewDidLoad ChatPage');
		// this.afDB.list('chats').push({
		// 	username:this.username,
		// 	message:"",
		// 	joined:true,
		// 	left:false		
		// });

		// setTimeout(() => {
		// 	this.content.scrollToBottom();
		// }, 300);
	}

	ionViewWillLeave(){
		this.afDB.list('chats').push({
			username:this.username,
			message:"",
			joined:false,
			left:true	
		});	
	}

	// scrollto() {
	// 	console.log('scrollto ChatPage');
	// 	setTimeout(() => {
	// 		this.content.scrollToBottom(1000);
	// 	}, 300);

	// 	// var element = document.getElementById("myLabel");
	// 	// // I can't remember why I added a short timeout, 
	// 	// // but you might be able to use ngzone instead.
	// 	// // the below works great though. 
	// 	// setTimeout(()=>{element.scrollIntoView(true)},200);
	// }
	
}



